import { PAGE_SIZE } from '@/assets/config'
import { CODE, SelectDataSource, TeachingMaterialConstructionKeys } from '@/assets/constant'
import tabsService from '@/assets/template/tabs'
import utilizeService from '@/assets/template/utilize'
import dataService from '@/assets/method/data'
import Cache from '@/assets/cache'
import Service from '@/assets/service/service'
export default {
  name: 'teachingMaterialConstructionTable',
  data () {
    return {
      code: CODE.TeachingMaterialConstruction,
      dataKeys: TeachingMaterialConstructionKeys,
      items: [],
      total: 0,
      page: 1,
      pageSize: PAGE_SIZE,
      operation: {},
      service: Service,
      condition: {
        page_num: PAGE_SIZE
      },
      searchOptions: [
        { label: '出版时间', key: 'time', type: 'date' },
        { label: '教材名称', key: 'name' },
        { label: '作者', key: 'author' },
        { label: '出版机构', key: 'unit' },
        { label: '教材级别', key: 'level', selectOption: SelectDataSource.Level }
      ]
    }
  },
  methods: {
    publicSearch (condition) {
      for (const key in condition) {
        this.condition[key] = condition[key]
      }
      this.getDataTables(this.page)
    },
    search () {
      this.getDataTables()
    },
    pageChange (page) {
      this.getDataTables(page)
    },
    getDataTables (page = 1) {
      this.pageSize = this.condition.page_num
      const request = {

      }
      if (page) {
        request['page'] = page
      }
      for (const key in this.condition) {
        if (this.condition[key]) {
          request[key] = this.condition[key]
        }
      }

      Service.TeachingMaterialConstruction.get(request).then(response => {
        if (response.code == 200) {
          this.page = page
          this.items = response.data.data
          this.total = response.data.total
          setTimeout(() => utilizeService.iCheck(), 200)
        }
      })
    },
    download (item) {
      this.fileExport({
        ids: [item.id],
        model: this.code
      })
    },
    fileExport (request) {
      Service.FileDownload(request).then(response => {
        dataService.download(response)
      })
    },
    delConfirm (item) {
      Service.TeachingMaterialConstruction.del(item.id).then(response => {
        if (response.code == 200) {
          this.getDataTables()
        }
      })
    },
    add () {
      tabsService.addDetailsTab('/views/teachingMaterialConstruction/new', '教材建设-新增')
    },
    edit (item) {
      tabsService.addDetailsTab('/views/teachingMaterialConstruction/' + item.id, '教材建设-详情-' + item.id)
    }
  },
  created () {
    this.operation = Cache.pageOperation('talent_development')
    dataService.getTableKeys(Service, this.code, this.dataKeys).then(dataKeys => {
      this.dataKeys = dataKeys
    })
    if (this.$route.query['key']) {
      this.condition['key'] = this.$route.query['key'];this.condition['status'] = 3
    }
    this.getDataTables()
  },
  mounted () {
  }
}



